  padding: 0.7rem 0;
  color: white;
}

.footer .footer_container {
  width: 95%;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.footer .footer_container .footer_info img {
  width: 100px;
}

.footer .footer_container .footer_info p {
  font-size: 12px;
}

.footer .footer_container .advertise h1 {
  font-size: 17px;
  padding: 0.7rem 1rem;
  background-color: #5e5e5e;
}
